import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import PropTypes from 'prop-types';

// material-ui
import { Button, Tooltip, LinearProgress } from '@mui/material';
import {
    Add as AddIcon,
    CloudQueue as CloudQueueIcon,
    FileUploadOutlined as FileUploadOutlinedIcon,
    DriveFileMoveOutlined as DriveFileMoveOutlinedIcon,
    CheckCircleOutlined as CheckCircleOutlinedIcon,
    ContentCopy as ContentCopyIcon,
    Speed as SpeedIcon,
    Check as CheckIcon,
    NetworkCheckOutlined as NetworkCheckOutlinedIcon,
    ArrowUpward as ArrowUpwardIcon,
    ArrowDownward as ArrowDownwardIcon,
    ModeEdit as EditIcon
} from '@mui/icons-material';
import {
    DataGrid,
    GridToolbarContainer,
    GridToolbarColumnsButton,
    GridToolbarFilterButton,
    GridToolbarExport,
    GridToolbarDensitySelector,
    zhCN
} from '@mui/x-data-grid';

let selectedArr = []; //å½“å‰é€‰ä¸­çš„äº§å“id
function ProductTable({
    tableRows,
    tableColumns,
    page_size,
    page,
    total,
    localization,
    langPack,
    columnVisibilityModel,
    setColumnVisibilityModel,
    handleChangePage,
    handleChangeRowsPerPage,
    onRowClick,
    loadingProgress,
    onTableSelection,
    productTableFn
}) {
    const navigate = useNavigate(); //react-router-dom
    let [updatedArr, setUpdatedArr] = useState([]); //æ›´æ–°äº†çš„è¡Œ
    let {
        scoreAutoQuery,
        setScoreAutoQuery,
        addProduct,
        updateProduct,
        changeProductDisplay,
        duplicateProduct,
        needPhoto,
        needSheet,
        needTemplate,
        needPost
    } = productTableFn;
    //DataGrid toolbar;
    const tableHeight = window.innerHeight < 700 ? 350 : window.innerHeight - 350;

    const onStateChange = (obj) => {
        let { selection } = obj;
        if (selection.length === selectedArr.length) return;
        selectedArr = selection.map((id) => tableRows.find((obj) => obj.id === id));
        onTableSelection(selectedArr);
    }; //å‹¾é€‰

    const onCellEditCommit = (params) => {
        let { id, row, field, value } = params;
        let oldIndex = tableRows.findIndex((obj) => obj.id === id);
        if (oldIndex === -1) return; //æ²¡æ‰¾åˆ°?
        let oldValue = tableRows[oldIndex][field];
        if (oldValue === value) return; //å…ˆå¯¹æ¯”åŸå§‹æ•°æ®ï¼Œå¦‚æœæ²¡æœ‰æ”¹å˜ï¼Œåˆ™ä¸æ›´æ–°
        let newData = { ...row, [field]: value };
        let newArr = [...updatedArr];
        newArr[oldIndex] = newData; //ä¸ç®¡æ”¹ä¸æ”¹ï¼Œå…ˆå­˜ç€å†è¯´
        return setUpdatedArr(newArr);
    };

    function getUpdatedTable(allRows) {
        let arr = allRows ? tableRows : selectedArr;
        return arr.map((rowObj) => {
            let id = Number(rowObj.id);
            return updatedArr[id] ? { ...rowObj, ...updatedArr[id] } : rowObj;
        });
    } //è·å–æ›´æ–°åçš„æ•°æ®

    return (
        <div style={{ height: tableHeight, width: '100%' }}>
            <DataGrid
                experimentalFeatures={{ newEditingApi: true }}
                checkboxSelection={true}
                localeText={localization.locale === 'zhCN' ? zhCN.components.MuiDataGrid.defaultProps.localeText : {}}
                components={{
                    Toolbar: () => (
                        <GridToolbarContainer>
                            <GridToolbarColumnsButton />
                            <GridToolbarFilterButton />
                            <GridToolbarDensitySelector />
                            <GridToolbarExport
                                csvOptions={{
                                    utf8WithBom: true,
                                    fileName:
                                        langPack.table.tableHeader +
                                        '_' +
                                        new Date(new Date().valueOf() - new Date().getTimezoneOffset() * 60 * 1000)
                                            .toISOString()
                                            .replace(/T.*/, '')
                                }}
                            />
                            <span hidden={setScoreAutoQuery === undefined}>
                                <Button
                                    sx={{ padding: '4px 5px', fontSize: '13px' }}
                                    startIcon={scoreAutoQuery ? <NetworkCheckOutlinedIcon /> : <SpeedIcon />}
                                    onClick={() => {
                                        setScoreAutoQuery((prev) => !prev);
                                    }}
                                >
                                    {langPack.tableToolbar.productScore}
                                </Button>
                            </span>
                            <span hidden={addProduct === undefined}>
                                <Button sx={{ padding: '4px 5px', fontSize: '13px' }} startIcon={<AddIcon />} onClick={addProduct}>
                                    {langPack.steps.addProduct}
                                </Button>
                            </span>
                            <span hidden={needTemplate === undefined}>
                                <Tooltip title={langPack.steps.bulkUploadTemplate}>
                                    <span>
                                        <Button sx={{ height: '100%' }} startIcon={<DriveFileMoveOutlinedIcon />} onClick={needTemplate}>
                                            {langPack.steps.template}
                                        </Button>
                                    </span>
                                </Tooltip>
                            </span>
                            <span hidden={needSheet === undefined}>
                                <Tooltip title={langPack.steps.uploadProductSheet}>
                                    <span>
                                        <Button sx={{ height: '100%' }} startIcon={<FileUploadOutlinedIcon />} onClick={needSheet}>
                                            {langPack.steps.import}
                                        </Button>
                                    </span>
                                </Tooltip>
                            </span>
                            <span hidden={needPost === undefined}>
                                <Tooltip title={langPack.steps.startPosting}>
                                    <span>
                                        <Button
                                            sx={{ height: '100%' }}
                                            startIcon={<CheckCircleOutlinedIcon />}
                                            onClick={() => needPost(getUpdatedTable(true))}
                                        >
                                            {langPack.steps.post}
                                        </Button>
                                    </span>
                                </Tooltip>
                            </span>
                            <span hidden={!selectedArr.length}>
                                <span hidden={changeProductDisplay === undefined}>
                                    <Button
                                        sx={{ padding: '4px 5px', fontSize: '13px' }}
                                        startIcon={<ArrowUpwardIcon />}
                                        onClick={() => {
                                            let list = selectedArr.map((obj) => obj.product_id).join(',');
                                            changeProductDisplay({
                                                new_display: 'on',
                                                product_id_list: list
                                            });
                                        }}
                                    >
                                        {langPack.tableToolbar.productDisplayOn}
                                    </Button>
                                    <Button
                                        sx={{ padding: '4px 5px', fontSize: '13px' }}
                                        startIcon={<ArrowDownwardIcon />}
                                        onClick={() => {
                                            let list = selectedArr.map((obj) => obj.product_id).join(',');
                                            changeProductDisplay({
                                                new_display: 'off',
                                                product_id_list: list
                                            });
                                        }}
                                    >
                                        {langPack.tableToolbar.productDisplayOff}
                                    </Button>
                                </span>
                                <span hidden={needPhoto === undefined}>
                                    <Tooltip title={langPack.steps.selectFromCloud}>
                                        <span>
                                            <Button
                                                disabled={selectedArr.length !== 1}
                                                sx={{ height: '100%' }}
                                                startIcon={<CloudQueueIcon />}
                                                onClick={() => needPhoto(getUpdatedTable())}
                                            >
                                                {langPack.steps.cloud}
                                            </Button>
                                        </span>
                                    </Tooltip>
                                </span>
                                <span hidden={duplicateProduct === undefined}>
                                    <Tooltip title={langPack.steps.duplicationLimitation}>
                                        <span>
                                            <Button
                                                sx={{ padding: '4px 5px', fontSize: '13px' }}
                                                startIcon={<ContentCopyIcon />}
                                                disabled={selectedArr.length !== 1}
                                                onClick={async () => {
                                                    if (!selectedArr.length) return;
                                                    await duplicateProduct(selectedArr[0]);
                                                    return;
                                                }}
                                            >
                                                {langPack.tableToolbar.productCopy}
                                            </Button>
                                        </span>
                                    </Tooltip>
                                </span>
                                <span hidden={addProduct === undefined}>
                                    <Button
                                        sx={{ padding: '4px 5px', fontSize: '13px' }}
                                        startIcon={<EditIcon />}
                                        disabled={selectedArr.length !== 1}
                                        onClick={() => {
                                            if (!selectedArr.length) return;
                                            let real_product_id = selectedArr.map((obj) => obj.id).join(',');
                                            let product_id = selectedArr.map((obj) => obj.product_id).join(',');
                                            navigate(`/product/real_product_id=${real_product_id}&product_id=${product_id}`);
                                            return;
                                        }}
                                    >
                                        {langPack.tableToolbar.productEdit}
                                    </Button>
                                </span>
                            </span>
                            <span hidden={!updatedArr.length}>
                                <span hidden={updateProduct === undefined}>
                                    <Button
                                        color="error"
                                        sx={{ padding: '4px 5px', fontSize: '13px' }}
                                        startIcon={<CheckIcon />}
                                        onClick={async () => {
                                            await updateProduct(updatedArr);
                                            setUpdatedArr([]);
                                        }}
                                    >
                                        {langPack.tableToolbar.productUpdate}
                                    </Button>
                                </span>
                            </span>
                        </GridToolbarContainer>
                    ),
                    LoadingOverlay: () => <LinearProgress variant="determinate" value={loadingProgress} />
                }}
                loading={loadingProgress !== 100}
                onCellEditCommit={onCellEditCommit}
                onStateChange={onStateChange}
                page={page}
                pageSize={page_size}
                rowCount={total}
                columns={tableColumns}
                columnVisibilityModel={columnVisibilityModel}
                onColumnVisibilityModelChange={setColumnVisibilityModel}
                getRowHeight={() => 88}
                rows={tableRows}
                rowsPerPageOptions={[10, 25, 100]}
                paginationMode="server"
                onRowClick={onRowClick}
                onPageChange={handleChangePage}
                onPageSizeChange={handleChangeRowsPerPage}
            />
        </div>
    );
}

ProductTable.propTypes = {
    onPhotoConfirm: PropTypes.func,
    tableRows: PropTypes.array,
    tableColumns: PropTypes.array,
    page_size: PropTypes.number,
    page: PropTypes.number,
    total: PropTypes.number,
    localization: PropTypes.object,
    langPack: PropTypes.object,
    columnVisibilityModel: PropTypes.object,
    setColumnVisibilityModel: PropTypes.func,
    loadingProgress: PropTypes.number,
    handleChangePage: PropTypes.func,
    handleChangeRowsPerPage: PropTypes.func,
    onRowClick: PropTypes.func,
    onTableSelection: PropTypes.func,
    productTableFn: PropTypes.object
};

export default ProductTable;
